.head{
    position: relative;
}
.header{
    background-color: #95ad00;
    width: 100%;
    height: auto;
    padding: 10px 0px;
   z-index: 2;
    position: fixed; 
}
.freeshop{
    text-align: left;
}
.linktophead{
    color: #ffff;
    text-decoration: none;
    padding: 0px 20px;

}
.freeshop span{
    color: #fff;
}
.freeshop{
    color: #fff;
    font-size: 13px;
}
.freeshop_right{
    text-align: right;
}
.logo{
    display:flex;
    align-items: center;
    justify-content: space-between;
}
.logoimg{
    width: 60px;
    height: auto;
    margin: 15px;
}
.logoName{
    color: #95ad00;
    font-size: 35px;
    font-weight: 500;
    
}
.linklogo{
    text-decoration: none;
}
.header_two{
    width: 100%;
    background-color: #fff;
    padding-top: 40px;
    box-shadow: 0px 3px 8px 0px lightgray;
    position: fixed;
    z-index: 1;
}
nav ul {
    list-style-type: none;
    display: flex;
    margin: 0;
    padding: 0;
    justify-content: space-around;
}
nav a{
    color: #4d5574;
    text-decoration: none;
    font-weight: 600;
    font-size: 14px;
}
nav a:hover{
    color: #95ad00;
    text-decoration: none;
    font-weight: 600;
    font-size: 14px;
}
.searchIcon{
    background-color: #f5f2ff;
    width: 40px;
    height: 40px;
    padding: 10px;
    border-radius: 50%;
}
.userIcon{
    background-color: #fff3ec;
    width: 40px;
    height: 40px;
    padding: 10px;
    border-radius: 50%;
}
.heartIcon{
    background-color: #ecfbff;
    width: 40px; 
    height: 40px;
    padding: 10px;
    border-radius: 50%;
}
.heartIcon2{
    /* background-color: #ecfbff; */
    color: #4d5574;
    width: 40px;
    height: 40px;
    padding: 5px;
    border-radius: 50%;
}
.bagIcon{
    background-color: #fdeecf;
    width: 40px;
    height: 40px;
    padding: 10px;
    border-radius: 50%;
}
.icons{
    display: flex;
    justify-content: space-evenly;
}
.addcartList{
    width: 400px;
    height: 94%;
    position: absolute;
    background-color: #ffffff;
    top: 100px;
    right: 0;
    position: fixed;
    /* display: none; */
    box-shadow:0px 0px 10px 1050px rgb(0 0 0 / 50%);
    z-index: 4;
    overflow: auto;
    
}
.listhead{
    background-color: #f2f2f6;
    padding: 10px;
}
.listbody{
    padding: 20px 50px;
    overflow-x: auto;
    
}
.itemaline{
    display: flex;
    justify-content: end;
    align-items: center;
}
.viewbutdiv{
    text-align: center;
}