.category-table {
    border-collapse: collapse;
    width: 100%;
  }
  
  .category-table th, .category-table td {
    border: 1px solid #ddd;
    padding: 8px;
    text-align: left;
  }
  
  .category-table th {
    background-color: #f2f2f2;
  }
  
  .sec_header{
    padding-top: 170px;
    position: relative;
  }

  
.category-container {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 30px; 
  list-style: none;
}

.category-box {
  border: 2px solid #ddd;
  padding: 15px;
  text-align: center;
  border-radius: 7px;
  box-shadow: 0px 0px 5px 2px #8080806e;
}

.category-box img {
  max-width: 100%;
  height: auto;
  margin-bottom: 10px;
}

.listImg{
  width: 80px;
  height: auto;
  margin: 10px;
}
.butCart{
  padding: 2px 5px;
  margin: 0px 5px;
}
.cartitemList{
 padding: 20px 40px;
 border: 2px solid black;
 background-color: #fff;
 z-index: 3;
 top: 125px;
 right: 0;
 position: fixed;
 width: 350px;
 height: 80%;
 text-align: center;
 overflow-y: auto;
 /* display: none; */

}
.ullist{
  margin-right: -20px;
  list-style: none;
}
.viewCart{
  padding: 10px 30px;
  border: 2px solid #95ad00;
  background: none;
  width: 250px;
  margin: 10px;
  border-radius: 30px;
  font-size: 20px;
  font-weight: 600;
  color: #95ad00;
}
.viewCart:hover{
  background-color: #95ad00;
  color: white;
}
.togglebag {
  position: absolute;
  z-index: 2;
  top: -105px;
  right: 38px;
  background-color: #fdeecf;
  width: 40px;
  height: 40px;
  padding: 10px;
  border-radius: 50%;
  /* position: fixed; */
}
.listcont{
  position: relative;
}